.lifestyle {
  width: 100%;
  background-color: rgba(243, 249, 236, 1);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-bottom:50px ;
}

.children {
  width: 100%;
  /* height: 550px; */
}

.imgDiv{
  display: flex;
  flex-direction: column;
  width: 63%;
  background-color: rgba(255, 255, 255, 1);
  border: 2px solid rgba(218, 220, 224, 1);
}

.lifestyle h1 {
  font-weight: 700;
  font-size: 28px;
  letter-spacing: 1px;
  color: rgba(0, 0, 0, 1);
  width: 55%;
  margin-top: 40px;
  text-align: center;
}

/* names */

.lifestyle .names {
  display: flex;
  flex-direction: column;
  width: 55%;
  padding: 20px 100px 30px 20px;
  background-color: rgba(255, 255, 255, 1);
  border: 2px solid rgba(218, 220, 224, 1);
  margin-top: 20px;
}

.names h2 {
  font-weight: 700;
  font-size: 18px;
  color: rgba(32, 33, 36, 1);
}

.names input {
  width: 80%;
  border: none;
  outline: none;
  border-bottom: 3px solid rgba(217, 217, 217, 1);
  padding: 10px 10px 10px 0;
  font-weight: 400;
  font-size: 20px;
  color: rgba(112, 117, 122, 1);
  letter-spacing: 0.33px;
}

.names input:focus {
  border-bottom: 3px solid rgba(255, 179, 0, 1);
}

/* games */

.lifestyle .game {
  display: flex;
  flex-direction: column;
  width: 55%;
  padding: 20px 100px 30px 20px;
  background-color: rgba(255, 255, 255, 1);
  border: 2px solid rgba(218, 220, 224, 1);
  margin-top: 20px;
  margin-top: 20px;
}

.game .concept {
  display: flex;
  flex-direction: column;
}

.game .concept h2 {
  font-weight: 700;
  font-size: 18px;
  color: rgba(32, 33, 36, 1);
  line-height: 25px;
}

.game .concept span {
  font-weight: 400;
  font-size: 14px;
  color: rgba(32, 33, 36, 1);
  margin-top: 20px;
  text-align: justify;
}

.game .upload {
  width: 90%;
  display: flex;
  align-items: center;
  margin-top: 20px;
  border: 2px dashed rgba(218, 220, 224, 1);
  padding: 10px 20px;
  justify-content: space-around;
  border-radius: 15px;
  position: relative;
}

.upload input {
  position: absolute;
  opacity: 0;
  padding: 50px 0;
  width: 137px;
  top: 31px;
  left: 22px;
  z-index: 5;
  cursor: pointer;
}

.upload-concept {
  display: flex;
  align-items: center;
}

.upload div:nth-child(1) {
  width: 70%;
}

.upload-concept div:nth-child(1) img {
  cursor: pointer;
  width: 60% !important;
}

.upload-concept div:nth-child(1) {
 
  width: 25%;
}

.upload-concept div:nth-child(2) {
  width: 70% !important;
}

.upload-concept div:nth-child(2) h4 {
  font-size: 18px;
  font-weight: 700;
  color: rgba(0, 0, 0, 1);
}

.upload-concept div:nth-child(2) p {
  line-height: 20px;
  font-weight: 400;
  font-size: 13px;
  color: rgba(111, 108, 108, 1);
}

.upload-concept div:nth-child(2) small {
  display: block;
  /* color: red; */
  font-size: 10px;
  line-height: 16px;
  margin-top: 5px;
  margin-left: 5px;
}

.upload-concept div:nth-child(2) small span {
  color: red;
  font-size: 12px;
}
.upload div:nth-child(2) {
  width: 25%;
}

.upload div:nth-child(2) button {
  border: 1px solid rgba(122, 183, 58, 1);
  width: 100%;
  height: 60px;
  border-radius: 10px;
  background-color: rgba(251, 253, 254, 1);
  color: rgba(122, 183, 58, 1);
  font-weight: 400;
  font-size: 15px;
  text-transform: uppercase;
  cursor: pointer;
}

.upload div:nth-child(2) button:hover {
  background-color: rgba(122, 183, 58, 1);
  color: rgba(251, 253, 254, 1);
  transition: all 0.5s linear;
}

.game .info {
  width: 100%;
  display: flex;
  flex-direction: column;
  margin-top: 20px;
}

.game .info span {
  font-weight: 600;
  font-size: 16px;
  color: rgba(111, 108, 108, 1);
  margin-top: 20px;
}

.game .info ul {
  display: contents;
}

.game .info ul li {
  font-weight: 400;
  font-size: 14px;
  color: rgba(111, 108, 108, 1);
  line-height: 25px;
  list-style: disc;
  margin-left: 30px;
  text-align: justify;
}

