/* agree */

.lifestyle .agree {
  display: flex;
  justify-content: space-between;
  width: 59.5%;
  align-items: center;

  margin-top: 20px;
  user-select: none;
}

.agree-text {
  /* width: 30%; */
  display: flex;
  align-items: center;
  user-select: none;
}

.lifestyle input[type="checkbox"] {
  appearance: none;

  border: 3px solid rgba(255, 179, 0, 1);
}
.lifestyle input[type="checkbox"]::before {
  content: "\2713";
  color: green;
  font-size: 13px;
  -webkit-transform: scale(0);
  transform: scale(0);
  -webkit-transition: 0.3s;
  transition: 0.3s;
  font-weight: 900;
}
.lifestyle input[type="checkbox"]:checked::before {
  -webkit-transform: scale(1);
  transform: scale(2);
}

.agree-text input {
  height: 25px;
  width: 25px;
  display: flex;
  justify-content: center;
}

.agree-text span {
  font-size: 18px;
  font-weight: 700;
  color: rgba(0, 0, 0, 1);
  letter-spacing: 1px;
  text-transform: capitalize;
  margin-left: 10px;
}

.term {
  /* width: 30%; */
}

.term a {
