/* l */

body.active-moidal {
  overflow-y: hidden;
}

.btn-modal {
  padding: 10px 20px;
  display: block;
  margin: 100px auto 0;
  font-size: 18px;
}

.modal,
.overlay {
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  position: fixed;
}

.overlay {
  background-color: rgba(49, 49, 49, 0.8);
}

.modal-content {
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -25%);
  line-height: 1.4;
  background-color: rgba(255, 255, 255, 1);
  padding: 30px 28px;
  border-radius: 3px;
  max-width: 600px;
  min-width: 300px;
  height: 300px;
  overflow-y: scroll;
}

.modal-content h2 {
  font-weight: 700;
  font-size: 20px;
  color: #ffb300;
  margin-top: 30px;
}

.firsttitle{
  margin: 0 !important;
}

.modal-content .acseptall,.acsept {
  margin-top: 10px;
  line-height: 25px;
  font-weight: 600;
}

.modal-content ul {
  margin-top: 20px;
  padding-left: 20px;
}

.modal-content ul li {
  list-style: decimal;
  font-weight: 500;
  color: #000;
  text-align: justify;
}

.modal-content ul li ul li {
  list-style: disc;
  text-align: justify;

}

.modal-content p{
  text-align: justify;
}
.close-modal {
  width: 30px;
  height: 30px;
  position: absolute;
  top: 0;
  right: 0;
  padding: 5px 7px;
  cursor: pointer;
}

/* error */
.errorfiles {
  border: 3px dashed red !important;
}

/* scroll styles */

.modal-content::-webkit-scrollbar {
  width: 8px; /* width of the scrollbar */
}

.modal-content::-webkit-scrollbar-track {
  background-color: #f1f1f1; /* color of the track */
}

.modal-content::-webkit-scrollbar-thumb {
  background-color: #ffb300; /* color of the scrollbar */
  border-radius: 4px; /* roundness of the scrollbar */
}

@media screen and (max-width: 978px) {
  .upload-concept {
    flex-direction: column;
    align-items: normal;
  }
  .upload-concept div:nth-child(2) {
    width: 95% !important;
    margin-top: 10px;
  }

  .upload div:nth-child(1) {
    width: 60%;
  }

  .upload div:nth-child(2) {
    width: 35%;
  }
  .upload-concept div:nth-child(1) img {
    width: 38% !important;
  }
}

@media screen and (max-width: 768px) {
