    line-height: 20px;
  }

  .lifestyle .names {
    width: 85%;
    padding: 30px;
    margin-top: 40px;
  }
  .lifestyle .game {
    width: 85%;

    padding: 30px;
  }
  .lifestyle .photos {
    width: 85%;

    padding: 30px;
  }
  .lifestyle .leaflet {
    width: 85%;

    padding: 30px;
  }

  .lifestyle .video {
    width: 85%;

    padding: 30px;
  }

  .lifestyle .agree {
    width: 90%;
  }

  .upload-concept div:nth-child(2) p {
    font-size: 12px;
  }

  .upload-concept div:nth-child(2) h4 {
    font-size: 14px;
    line-height: 18px;
  }

  .game .info span {
    font-size: 14px;
    letter-spacing: 0.5px;
  }

  .game .info ul li {
    line-height: 20px;
    font-size: 12px;
  }

  .leaflet .info span {
    font-size: 14px;
  }

  .leaflet .info ul li {
    font-size: 12px;
  }

  .video .info span {
    font-size: 14px;
  }

  .video .info ul li {
    font-size: 12px;
  }

  .agree-text span {
    font-size: 14px;
  }

  .term a {
    font-size: 14px;
  }
  .modal-content ul li{
    font-size: 12px;
  }

  .modal-content p{
