  }

  .video .upload {
    flex-direction: column;
  }

  .upload div:nth-child(1) {
    width: 100%;
  }
  .upload-concept div:nth-child(1) img {
    width: 15% !important;
  }

  .upload div:nth-child(2) {
    width: 100%;
  }
  .upload div:nth-child(2) button {
    font-size: 11px;
    height: 45px;
    margin-top: 20px;
  }

  
}


@media screen and (max-width:410px) {
